/* Pagination Styles */
.pagination {
  display: flex;
  justify-content: center;
  list-style: none;
  padding: 10px 0;
}

.pagination li {
  margin: 0 5px;
  cursor: pointer;
}

.pagination li a {
  text-decoration: none;
  padding: 8px 12px;
  border-radius: 5px;
  background: #f8f9fa;
  color: #393939 !important;
  border: 1px solid #007bff;
  transition: all 0.3s ease-in-out;
}

.pagination li a:hover {
  background: #007bff;
  color: white !important;
}

.pagination .active a {
  background: #007bff;
  color: white;
}

/* Search Suggestion Box */
.list-group {
  z-index: 1000;
  max-height: 200px;
  overflow-y: auto;
}

.list-group-item {
  border: 1px solid #ddd;
  padding: 10px;
}

.list-group-item:hover {
  background-color: #f1f1f1;
}

/* Form and Table */
.card {
  background: white;
  border-radius: 8px;
  padding: 20px;
}

.table {
  margin-top: 20px;
}

.table th,
.table td {
  text-align: center;
}

/* Button Styles */
.btn-warning {
  background-color: #ffc107;
  color: black;
}

.btn-danger {
  background-color: #dc3545;
}

.btn-primary {
  background-color: #007bff;
}

.btn-warning:hover,
.btn-danger:hover,
.btn-primary:hover {
  opacity: 0.8;
}
