/* General Styling */
 
  
  /* Form Styling */
  .faq-form {
    max-width: 600px;
    margin: 0 auto;
    background: #f8f9fa;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .faq-form h3 {
    text-align: center;
  }
  
  .faq-form .form-group {
    margin-bottom: 15px;
  }
  
  .faq-form input,
  .faq-form textarea {
    font-size: 16px;
    padding: 10px;
    width: 100%;
    border-radius: 5px;
    border: 1px solid #ccc;
  }
  
  /* Table Styling */
  .table-responsive {
    overflow-x: auto;
  }
  
  .table th,
  .table td {
    text-align: center;
    vertical-align: middle;
    padding: 12px;
  }
  
  .table th {
    background-color: #007bff;
    color: white;
  }
  
  /* Search Bar */
  .search-bar {
    max-width: 600px;
    margin: 0 auto 15px;
  }
  
  /* Pagination */
  .ant-pagination {
    text-align: center;
    margin-top: 20px;
  }
  
  .ant-pagination-item-active {
    background-color: #007bff !important;
    border-color: #007bff !important;
  }
  
  .ant-pagination-item-active a {
    color: white !important;
  }
  
  /* Responsive Adjustments */
  @media (max-width: 768px) {
    .faq-form {
      width: 100%;
      padding: 15px;
    }
  
    .table th,
    .table td {
      font-size: 14px;
      padding: 8px;
    }
  
    .btn {
      font-size: 12px;
      padding: 6px 10px;
    }
  }
  
  @media (max-width: 480px) {
    .faq-form {
      width: 100%;
      padding: 10px;
    }
  
    .table th,
    .table td {
      font-size: 12px;
      padding: 6px;
    }
  
    .btn {
      font-size: 10px;
      padding: 5px 8px;
    }
  }
  